@import "../../App.scss";
.programming {
  &__container {
    font-family: "Proxima Nova Regular", sans-serif;
    background: url("../../assets/images/about-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 120px 80px 0 60px;
    width: 100%;
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    margin-top: -20px;
  }
}

@media all and (max-width: 1280px) {
  .programming {
    &__container {
      padding: 30px 0 0 50px;
      height: 100%;
    }
    &__inner {
      margin-top: 0;
    }
  }
}

@media all and (max-width: 1024px) {
  .programming {
    &__container {
      @include adaptiveValue("padding-inline", 50, 30, 1024, 768);
    }
    &__inner {
      justify-content: space-evenly;
      row-gap: 20px;
    }
  }
}

@media all and (max-width: 480px) {
  .programming {
    &__container {
      padding-inline: 15px;
    }
  }
}
