@import "../../App.scss";
.navbar {
  &__list {
    color: #fff;
    font-size: 2rem;
    font-family: "Proxima Nova Regular", sans-serif;
    line-height: 36px;
    text-decoration: none;
    height: 350px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 0;
  }
  &__item {
    cursor: pointer;
    padding: 25px 0 25px 45px;
    overflow-x: hidden;
    text-decoration: none;
    color: #ffffff;
    &:hover {
      background: rgba(255, 255, 255, 0.03137254901960784);
    }
    &:focus {
      background: rgba(255, 255, 255, 0.03137254901960784);
      outline: none;
      border: none;
    }
    &.active {
      background: rgba(255, 255, 255, 0.03137254901960784);
      padding-left: 40px;
      color: rgba(255, 75, 168, 1);
      border-left: 5px solid rgba(255, 75, 168, 1);
    }
  }
}

@media all and (max-width: 1440px) {
  .navbar {
    &__container {
      padding: 0;
    }
  }
}

@media all and (max-width: 1280px) {
  .navbar {
    &__container {
      height: 100px;
      display: flex;
      align-items: center;
    }
    &__list {
      flex-direction: row;
      height: min-content;
      width: 600px;
      justify-content: space-between;
      margin: 0;
    }
    &__item {
      overflow-y: hidden;
      position: relative;
      padding: 0;
      transition: color 0.3s ease;
      padding-bottom: 1px;
      z-index: 2;
      background: none;
      &:focus,
      &:hover {
        background: none;
      }
      &.active {
        padding: 0px;
        background: none;
        border: none;
      }

      &::after {
        content: "";
        display: block;
        position: absolute;
        right: 0;
        bottom: -3px;
        width: 0;
        height: 5px;
        background-color: rgb(223, 53, 141);
        transition: width 0.5s;
        padding: 0;
      }
      &.active::after {
        content: "";
        display: block;
        position: absolute;
        right: 0;
        bottom: -3px;
        width: 100%;
        height: 5px;
        background-color: rgb(223, 53, 141);
        padding: 0;
      }
      &:hover {
        transition: all 0.5s;
        background: none;
      }
      &:hover::after {
        content: "";
        padding: 0;
        width: 100%;
        display: block;
        position: absolute;
        left: 0;
        bottom: -3px;
        height: 5px;
        background-color: rgb(223, 53, 141);
        transition: width 0.5s;
      }
    }
  }
}

@media all and (max-width: 1024px) {
  .navbar {
    &__list {
      @include adaptiveValue("font-size", 32, 22, 1024, 768);
      @include adaptiveValue("width", 600, 400, 1024, 768);
    }
  }
}

@media all and (max-width: 768px) {
  .navbar {
    &__container {
      position: relative;
      padding: 50px 30px 0 0;
    }
    &__list {
      flex-direction: column;
      position: absolute;
      top: 30px;
      font-size: 1.8rem;
      width: fit-content;
    }
    &__item {
      position: static;
      padding-left: 30px;
      padding-block: 15px;
      cursor: pointer;
      &:hover {
        background: rgba(255, 255, 255, 0.03137254901960784);
        &::after {
          display: none;
        }
      }
      &:focus {
        background: rgba(255, 255, 255, 0.03137254901960784);
        outline: none;
        border: none;
      }
      &.active {
        padding-left: 25px;
        padding-block: 15px;
        background: rgba(255, 255, 255, 0.03137254901960784);
        color: rgba(255, 75, 168, 1);
        border-left: 5px solid rgba(255, 75, 168, 1);
        &::after {
          display: none;
        }
      }
      &::after {
        display: none;
      }
    }
  }
}

@media all and (max-width: 540px) {
  .navbar {
    &__list {
      width: 100%;
      @include adaptiveValue("font-size", 29, 24, 540, 480);
    }
  }
}

@media all and (max-width: 480px) {
  .navbar {
    &__list {
      font-size: 18px;
    }
    &__item {
      padding-left: 15px;
      &.active {
        padding-left: 10px;
      }
    }
  }
}
