@import "../../App.scss";

.linear-settings {
  position: fixed !important;
  top: 0;
  left: 0;
  right: 0;
  max-height: 4px;
}

.contact {
  &__container {
    font-family: "Proxima Nova Regular", sans-serif;
    background: url("../../assets/images/about-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 120px 80px 0 60px;
    width: 100%;
  }

  &__form {
    width: 100%;
    display: flex;
    flex-direction: column;
    color: #ffffff;
  }
}
.form {
  &__container {
    z-index: 4;
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
    max-width: 1000px;
    font-family: "Proxima Nova Regular", sans-serif;
  }
  &__label {
    font-size: 2rem;
    line-height: 30px;
    margin-bottom: 35px;
  }
  &__textarea {
    resize: none;
    max-width: 1000px;
    min-height: 250px;
    width: 100% !important;
    height: 100% !important;
    &::placeholder {
      font-family: "Proxima Nova Regular", sans-serif;
      color: #ffffff;
      font-size: 1.2rem;
    }
  }
  &__inner {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  &__input {
    width: 470px;
    height: 80px;
    background-color: rgb(36, 38, 59) !important;
    opacity: 0.33;
    border: 0.6px solid rgba(255, 255, 255, 1);
    outline: none;
    padding: 30px 30px;
    font-size: 1.2rem;
    color: #ffffff;
    &:hover,
    &:focus,
    &:active {
      background-color: rgb(36, 38, 59) !important;
      color: #ffffff;
    }
    &::placeholder {
      font-family: "Proxima Nova Regular", sans-serif;
      color: #ffffff;
      font-size: 1.2rem;
    }
  }
  &__social {
    display: flex;
    align-items: center;
    flex-direction: row;
    max-width: 600px;
    margin-top: 20px;
    margin-bottom: 0;
  }
}

.btn {
  &__container {
    display: flex;
    align-items: center;
  }
  position: relative;
  width: 180px;
  height: 60px;
  cursor: pointer;
  background: transparent;
  border: 1px solid #ff4ba8;
  outline: none;
  transition: 1s ease-in-out;
  &:hover {
    transition: 1s ease-in-out;
    background: #ff4ba8;
  }
  &:hover svg {
    stroke-dashoffset: -480;
  }
  & span {
    z-index: 2;
    text-decoration: none;
    color: white;
    font-size: 18px;
    font-weight: 100;
  }
}
.btn-svg {
  position: absolute;
  left: 0;
  top: 0;
  fill: none;
  stroke: #fff;
  stroke-dasharray: 150 480;
  stroke-dashoffset: 150;
  transition: 1s ease-in-out;
}

@media all and (max-width: 1440px) {
  .form {
    &__input {
      @include adaptiveValue("width", 470, 420, 1440, 1280);
    }
  }
}

@media all and (max-width: 1280px) {
  .contact {
    &__container {
      padding: 30px 0 0 50px;
      height: 100%;
    }
    &__form {
      margin: 0;
      justify-content: space-evenly;
      height: 98%;
    }
  }
  .form {
    &__container {
      @include adaptiveValue("max-width", 1000, 800, 1280, 1024);
      margin: 0;
    }
    &__input {
      @include adaptiveValue("width", 460, 370, 1280, 1024);
    }
    &__social {
      @include adaptiveValue("max-width", 600, 600, 1280, 1024);
    }
  }
}

@media all and (max-width: 768px) {
  .btn-svg {
    display: none;
  }
}

@media all and (max-width: 540px) {
  .contact {
    &__container {
      @include adaptiveValue("padding-inline", 50, 30, 540, 480);
    }
  }
  .form {
    &__container {
      max-width: none;
    }
    &__social {
      &_hidden {
        display: none;
        visibility: hidden;
      }
    }
    &__input {
      line-height: 27px;
      padding-block: 15px;
      height: 60px;
      padding-inline: 20px;
      font-size: 1rem;
      &::placeholder {
        font-size: 1rem;
      }
    }
    &__textarea {
      &::placeholder {
        font-size: 1.1rem;
      }
    }
  }
}

@media all and (max-width: 480px) {
  .contact {
    &__form {
      padding-inline: 15px;
    }
  }
  .form {
    &__inner {
      flex-direction: column;
      .form__input {
        margin-bottom: 15px;
      }
    }
    &__textarea {
      min-height: 150px;
    }
    &__input {
      max-width: 100%;
      width: 100%;
    }
    &__container {
      .btn {
        width: 100%;
      }
    }
  }
}
