.social {
  &__list {
    display: flex;
    margin-inline: auto;
    flex-direction: row;
    justify-content: space-between;
    max-width: 200px;
    padding: 0;
    width: 100%;
    list-style: none;
  }
  &__item a {
    font-size: 27px;
    color: rgba(255, 255, 255, 0.16);
    transition: 0.3s ease-out;
    &:focus {
      outline: none;
    }
    svg {
      overflow-x: visible;
      transition: 0.3s ease-out;
    }
    &:hover svg {
      transition: color 0.3 ease;
      transform: scale(1.2);
      transition: 0.3s ease-out;
      border-radius: 20px;
      color: rgba(255, 75, 168, 0.6);
    }
    &:focus svg {
      transform: scale(1.2);
      transition: 0.3s ease-out;
      color: rgba(255, 75, 168, 0.6);
    }
  }
}

