:root {
  --toastify-color-success: #ff4ba8 !important;
  --toastify-color-error: #e74c3c !important;
  --toastify-icon-color-success: var(--toastify-color-success) !important;
}

@font-face {
  font-family: "Proxima Nova Regular";
  font-style: normal;
  font-weight: normal;
  src: url("./assets/fonts/ProximaNova-Regular.ttf");
}

@font-face {
  font-family: "Proxima Nova SemiBold";
  font-style: normal;
  font-weight: 600;
  src: url("./assets/fonts/ProximaNova-Semibold.ttf");
}

@font-face {
  font-family: "Proxima Nova Bold";
  font-style: normal;
  font-weight: bold;
  src: url("./assets/fonts/ProximaNova-Bold.ttf");
}

@font-face {
  font-family: "Proxima Nova Thin";
  font-style: normal;
  font-weight: 200;
  src: url("./assets/fonts/ProximaNova-Thin.ttf");
}

* {
  //overflow-y:hidden;
  box-sizing: border-box;
}
#root,
.App,
html {
  height: 100%;
}
body {
  margin: 0;
  font-family: "Proxima Nova Regular", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

body ::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 100%, 0.16);
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

::-webkit-scrollbar-track {
  background: hsla(0, 0%, 100%, 0.1);
}

body {
  background-image: url("assets/images/background.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
}
:is(aside) {
  overflow-y: hidden;
}
section {
  overflow-x: hidden;
}
