@import "../../App.scss";
.sidebar {
  &__container {
    overflow-x: hidden;
    top: 0;
    bottom: 0;
    box-sizing: border-box;
    max-width: 20%;
    width: 100%;
    padding: 125px 0 0 0;
    max-height: 970px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }
  &__img {
    --border-rotate: 0;
    --border-width: 3px;
    position: relative;
    margin-inline: auto;
    background: url("../../assets/images/profilePhoto.jpeg");
    background-size: cover;
    background-repeat: no-repeat;
    height: 200px;
    width: 200px;
    border-radius: 50%;
    &::before {
      position: absolute;
      inset: calc(var(--border-width) * -0.5);
      padding: var(--border-width);
      border-radius: inherit;
      background: conic-gradient(
        from calc(var(--border-rotate, 0) * 1deg),
        rgba(30, 26, 52, 1),
        #2f234a,
        #432c60,
        #5c3377,
        #793a8e,
        #983fa4,
        #d247b8,
        #e849b3,
        #ff4ba8
      );
      -webkit-mask: linear-gradient(#fff 0 0) content-box,
        linear-gradient(#fff 0 0);
      -webkit-mask-composite: xor;
      mask-composite: exclude;
      content: "";
    }
  }
  &__name {
    color: #fff;
    font-size: 1.5rem;
  }
  &__option {
    width: 40px;
    height: 30px;
    position: relative;
    margin: 30px 0;
    transform: rotate(0deg);
    transition: 0.5s ease-in-out;
    cursor: pointer;
    span {
      display: block;
      position: absolute;
      height: 3px;
      width: 100%;
      background: #fff;
      border-radius: 9px;
      opacity: 1;
      left: 0;
      transform: rotate(0deg);
      transition: 0.25s ease-in-out;
      &:nth-child(1) {
        top: 0px;
      }
      &:nth-child(2) {
        top: 10px;
      }
      &:nth-child(3) {
        top: 20px;
      }
    }
    &.open span:nth-child(1) {
      top: 10px;
      transform: rotate(135deg);
    }
    &.open span:nth-child(2) {
      opacity: 0;
      left: -60px;
    }
    &.open span:nth-child(3) {
      top: 10px;
      transform: rotate(-135deg);
    }
  }
  &__nav {
    &_open,
    &_close {
      display: block;
    }
  }
  &__option {
    display: none;
  }
}

@media all and (max-width: 1280px) {
  .sidebar {
    &__container {
      max-width: 100%;
      flex-direction: row;
      padding: 0;
      padding-inline: 50px;
      min-height: 100px;
    }
    &__img {
      display: none;
    }
    &__social {
      width: 140px;
      margin-block: auto;
    }
  }
}

@media all and (max-width: 768px) {
  .sidebar {
    &__container {
      min-height: 80px;
      flex-direction: row-reverse;
    }
    &__option {
      display: block;
    }
    &__nav {
      &_close {
        border-right: 1px solid #fff;
        position: fixed;
        height: 100vh;
        width: 40vw;
        z-index: 33;
        transform: translateX(-700px);
        transition: all 0.3s ease;
        left: 0;
      }
      &_open {
        display: flex;
        flex-direction: column;
        border-right: 1px solid #fff;
        background-color: rgb(36, 38, 59) !important;
        opacity: 0.9;
        position: fixed;
        height: 100vh;
        width: 40vw;
        z-index: 33;
        transition: all 0.3s ease;
        left: 0;
      }
    }
    &__close-btn {
      cursor: pointer;
      height: 20px !important;
      width: 24px;
      display: flex;
      flex-direction: row-reverse;
      font-size: 30px;
      line-height: 23px;
      position: absolute;
      top: 40px;
      right: 30px;
      &::after,
      &::before {
        content: "";
        position: absolute;
        width: 24px;
        height: 4px;
        background: #fff;
      }
      &::after {
        transform: rotate(45deg);
      }
      &::before {
        transform: rotate(-45deg);
      }
    }
  }
}

@media all and (max-width: 540px) {
  .sidebar {
    &__container {
      @include adaptiveValue("padding-inline", 50, 30, 540, 480);
    }
  }
}

@media all and (max-width: 480px) {
  .sidebar {
    &__container {
      min-height: fit-content;
      @include adaptiveValue("padding-inline", 30, 15, 480, 320);
      @include adaptiveValue("height", 80, 65, 480, 320);
    }
    &__nav {
      &_open {
        width: 50vw;
      }
    }
    &__option {
      @include adaptiveValue("margin-top", 30, 18, 480, 320);
      @include adaptiveValue("width", 40, 35, 480, 320);
    }
  }
}

@media all and (max-width: 320px) {
  .sidebar {
    &__option {
      margin-top: 18px;
    }
  }
}
