@import "../../../App.scss";
.projects {
  &__container {
    color: #fff;
    font-family: "Proxima Nova Regular", sans-serif;
    font-size: 30px;
    line-height: 23px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 40px;
  }
  &__card {
    max-width: 480px;
    width: 100%;
    min-height: 480px;
    background: rgba(30, 26, 52, 1);
  }
  &__text {
    padding: 30px 40px 40px 40px;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: space-between;
    min-height: 190px;
  }
  &__image {
    position: relative;
    overflow: hidden;
    max-height: 300px;
    height: 100%;
    width: 100%;
    background: transparent;
    img {
      object-fit: cover;
      position: absolute;
      width: 100%;
      height: 100%;
    }
  }
  &__descr {
    font-size: 20px;
    opacity: 0.2;
  }
  &__link {
    font-size: 14px;
    color: #ff4ba8;
    text-decoration: none;
    &:focus {
      outline: none;
    }
    &:hover {
      &.arrow-right {
      }
    }
  }
}
.arrow-right {
  margin-left: 5px;
  border: solid #ff4ba8;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(-45deg);
}

@media all and (max-width: 1440px) {
  .projects {
    &__card {
      @include adaptiveValue("max-width", 480, 415, 1440, 1280);
      @include adaptiveValue("min-height", 480, 470, 1440, 1280);
    }
  }
}

@media all and (max-width: 1024px) {
  .projects {
    &__container {
      display: flex;
      justify-content: space-around;
    }
    &__card {
      @include adaptiveValue("max-width", 400, 300, 1024, 768);
    }
  }
}

@media all and (max-width: 768px) {
  .projects {
    &__container {
      width: 100%;
    }
    &__card {
      max-width: 100%;
    }
    &__text {
      padding-block: 20px;
      min-height: 135px;
    }
  }
}

@media all and (max-width: 480px) {
  .projects {
    &__card {
      min-height: fit-content;
      height: 280px;
    }
    &__image {
      height: 50%;
    }
    &__text {
      padding: 15px 20px;
    }
    &__title {
      font-size: 24px;
    }
    &__descr {
      font-size: 16px;
      opacity: 0.3;
    }
  }
}
