@import "../../App.scss";
.resume {
  &__container {
    font-family: "Proxima Nova Regular", sans-serif;
    background: url("../../assets/images/about-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 120px 80px 0 60px;
    width: 100%;
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-height: 960px;
  }
  &__card {
    margin-top: 40px;
    max-height: 550px;
    max-width: 1000px;
    width: 100%;
    z-index: 3;
  }
  &__titles {
    max-width: 1000px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    &__options {
      position: relative;
      transition: color 0.3s ease;
      z-index: 2;
      cursor: pointer;
      color: #fff;
      &_selected {
        color: rgb(223, 53, 141);
      }
      &::after {
        content: "";
        display: block;
        position: absolute;
        right: 0;
        bottom: -3px;
        width: 0;
        height: 2px;
        background-color: rgb(223, 53, 141);
        transition: width 0.5s;
      }
      &:hover {
        transition: all 0.5s;
      }
      &:hover::after {
        content: "";
        width: 100%;
        display: block;
        position: absolute;
        left: 0;
        bottom: -3px;
        height: 2px;
        background-color: rgb(223, 53, 141);
        transition: width 0.5s;
      }
    }
  }
  &__icons {
    padding-right: 10px;
  }
}

@media all and (max-width: 1280px) {
  .resume {
    &__container {
      padding: 30px 0 0 50px;
      height: 100%;
    }
    &__inner {
      margin-top: 0;
    }
    &__titles {
      @include adaptiveValue("max-width", 1000, 800, 1280, 1024);
    }
  }
}

@media all and (max-width: 1024px) {
  .resume {
    &__card {
      padding: 0;
      padding-right: 50px;
    }
  }
}

@media all and (max-width: 768px) {
  .resume {
    &__container {
      padding-inline: 50px;
    }
    &__titles {
      max-width: 668px;
    }
    &__card {
      padding-right: 0;
    }
  }
}

@media all and (max-width: 540px) {
  .resume {
    &__container {
      @include adaptiveValue("padding-inline", 50, 30, 540, 480);
    }
  }
}

@media all and (max-width: 480px) {
  .resume {
    &__container {
      padding-inline: 15px;
    }
    &__card {
      margin-top: 20px;
    }
  }
}
