@import "../../App.scss";
.about {
  &__container {
    font-family: "Proxima Nova Regular", sans-serif;
    background: url("../../assets/images/about-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 5% 0 0 60px;
  }

  &__inner {
    padding-bottom: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    min-height: 680px;
    // height: 82%;
  }

  &__info {
    display: flex;
    flex-direction: column;
  }

  &__name {
    font-family: "Proxima Nova Regular", sans-serif;
    color: #ffffff;
    font-size: 4.5rem;
    line-height: 60px;
    margin-bottom: 30px;

    span {
      color: rgba(223, 53, 141, 1);
      font-size: 5rem;
    }
  }

  &__description {
    font-family: "Proxima Nova Semibold", sans-serif;
    font-size: 2rem;
    font-weight: 500;
    line-height: 40px;
    color: #ff68b6;
  }

  &__text {
    width: 50%;
    font-family: "Proxima Nova Regular", sans-serif;
    font-weight: 300;
    font-size: 1.5rem;
    color: #ffffff;
    opacity: 0.6;
    max-width: 80%;
  }

  &__qoute {
    margin-top: auto;
    height: 70px;
    width: fit-content;
    background-image: url("../../assets/images/q.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: left center;
    color: #ffffff;
    padding-block: 15px;
    font-size: 2rem;

    span {
      padding-left: 100px;
      opacity: 0.25;
    }
  }
  &__btn {
    text-decoration: none;
    &_more {
      cursor: pointer;
      margin-left: 20px;
      color: #ffffff;
      background: transparent;
      width: 180px;
      height: 60px;
      border: 0 solid;
      box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
      outline: 1px solid;
      outline-color: rgba(255, 255, 255, 1);
      outline-offset: 0px;
      text-shadow: none;
      transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
      &:hover {
        transition: all 0.5s;
        border: 1px solid;
        box-shadow: inset 0 0 20px rgba(255, 255, 255, 0.5),
          0 0 20px rgba(255, 255, 255, 0.2);
        outline-color: rgba(255, 255, 255, 0);
        outline-offset: 15px;
        text-shadow: 1px 1px 2px #427388;
      }
      & a {
        text-decoration: none;
        color: white;
        font-size: 18px;
        // font-weight: 100;
      }
    }
    &_hire {
      position: relative;
      top: 16px;
      padding-left: 55px;
      font-weight: 500 !important;
    }
  }
}

@media all and (max-width: 1280px) {
  .about {
    &__container {
      padding: 30px 0 0 50px;
      height: 100%;
    }
    &__inner {
      margin-top: 0;
      height: 98%;
      justify-content: space-between;
    }
    &__info {
      margin-top: 100px;
      @include adaptiveValue("margin-top", 100, 0, 1280, 1024);
    }
    &__qoute {
      margin: 0;
      margin-bottom: 20px;
    }
  }
}

@media all and (max-width: 1024px) {
  .about {
    &__name {
      @include adaptiveValue("font-size", 72, 48, 1024, 768);
      span {
        @include adaptiveValue("font-size", 72, 48, 1024, 768);
      }
    }
    &__text {
      @include adaptiveValue("font-size", 24, 20, 1024, 768);
    }
    &__info {
      @include adaptiveValue("margin-top", 0, 0, 1024, 768);
    }
  }
}

@media all and (max-width: 768px) {
  .about {
    &__name {
      @include adaptiveValue("font-size", 48, 48, 768, 540);
      span {
        @include adaptiveValue("font-size", 48, 48, 768, 540);
      }
    }
    &__text {
      @include adaptiveValue("font-size", 24, 23, 1024, 768);
    }
  }
}
@media all and (max-width: 540px) {
  .about {
    &__container {
      @include adaptiveValue("padding-inline", 50, 30, 540, 480);
    }
    &__text {
      width: 100%;
    }
    &__qoute {
      height: 50px;
      // @include adaptiveValue("font-size",32,24,540,540)
      font-size: 1.5rem;
      span {
        padding-left: 70px;
      }
    }
  }
}

@media all and (max-width: 480px) {
  .about {
    &__inner {
      min-height: fit-content;
    }
    &__container {
      padding-inline: 15px;
    }
    &__description {
      font-size: 1.7rem;
    }
    &__text {
      max-width: 100%;
    }
    &__qoute {
      font-size: 1.2rem;
      margin-bottom: 0;
    }
  }
}
