@import "../../../App.scss";
.education {
  &__container {
    width: 100%;
    max-width: 1000px;
    display: flex;
    flex-direction: column;
  }
  &__list {
    list-style: none;
    padding: 0;
  }
  &__item {
    position: relative;
    background: rgb(30, 26, 52);
    border-radius: 10px;
    max-width: 1000px;
    width: 100%;
    padding: 20px;
    border: 1px solid black;
    margin-bottom: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    overflow: hidden;
    &:hover {
      transition: all 0.3s ease;
      span {
        color: rgb(223, 53, 141);
        transition: all 0.3s ease;
      }
      // &::before {
      //     content: "";
      //     transition: all .3s ease;
      //     z-index: 1;
      //     position: absolute;
      //     left: var(--x);
      //     top: var(--y);
      //     width: var(--size);
      //     height: var(--size);
      //     background: radial-gradient(circle closest-side, #4405f7, transparent);
      //     -webkit-transform: translate(-50%, -50%);
      //     transform: translate(-50%, -50%);
      //     transition: width 0.2s ease, height 0.2s ease;}
    }
  }
  &__university {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__date {
    color: #fff;
  }
}
.university {
  &__name {
    color: #fff;
    font-size: 18px;
    margin-bottom: 10px;
  }

  &__faculty {
    color: #fff;
    opacity: 0.6;
    font-size: 12px;
  }
}

@media all and (max-width: 1280px) {
  .education {
    &__container {
      @include adaptiveValue("max-width", 1000, 600, 1280, 1024);
    }
  }
}
@media all and (max-width: 1024px) {
  .education {
    &__container {
      max-width: none;
      width: 600px;
    }
  }
}
@media all and (max-width: 768px) {
  .education {
    &__container {
      width: 100%;
    }
    &__university {
      max-width: 70%;
    }
  }
}

@media all and (max-width: 480px) {
  .education {
    &__item {
      padding: 10px;
    }
    &__university {
      max-width: 65%;
    }
  }
}
