@import "../../App.scss";
.programming-card {
  &__container {
    border: 0.5px solid rgba(0, 0, 0, 0.5);
    height: 280px;
    border-radius: 20px;
    background-color: rgba(36, 38, 59, 0.33) !important;

    color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    @include adaptiveValue("width", 325, 300, 1920, 1440);
    @include adaptiveValue("margin", 20, 15, 1920, 1440);
    margin-bottom: 0;
  }

  &__image {
    cursor: pointer;
    position: relative;
    width: 88px;
    height: 88px;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    margin-bottom: 15px;

    img {
      border-radius: 10px;
    }
    &:hover {
      transform: rotateY(180deg);
    }
  }
  &__description {
    text-align: center;
  }
}

@media all and (max-width: 768px) {
  .programming-card {
    &__container {
      @include adaptiveValue("width", 265, 220, 768, 480);
      @include adaptiveValue("height", 265, 220, 768, 480);
    }
  }
}

@media all and (max-width: 480px) {
  .programming-card {
    &__container {
      width: 90%;
      height: 240px;
    }
  }
}
