@import "../../../App.scss";
.work {
  &__contaianer {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  &__list {
    list-style: none;
    padding: 0;
  }
  &__item {
    background: rgb(30, 26, 52);
    border-radius: 10px;
    max-width: 1000px;
    width: 100%;
    padding: 20px;
    border: 1px solid black;
  }
  &__company {
    display: flex;
    flex-direction: column;
  }
}
.company {
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__name {
    color: #fff;
    font-size: 24px;
    margin-bottom: 10px;
    transition: color 0.3s ease;
    &:hover {
      transition: color 0.3s ease;
      color: rgb(223, 53, 141);
    }
    &:focus {
      transition: color 0.3s ease;
      color: rgb(223, 53, 141);
      outline: none;
    }
  }
  &__date {
    color: #fff;
  }
  &__position {
    color: #fff;
    font-size: 18px;
  }
  &__descr {
    &-list {
      margin-top: 10px;
    }
    &-item {
      color: #fff;
      font-size: 16px;
    }
  }
}

@media all and (max-width: 1280px) {
  .work {
    &__container {
      @include adaptiveValue("max-width", 1000, 600, 1280, 1024);
    }
  }
  .company {
    &__descr {
      max-width: 70%;
    }
    &__info {
      margin-bottom: 20px;
    }
  }
}
@media all and (max-width: 1024px) {
  .work {
    &__container {
      max-width: none;
      width: 600px;
    }
  }
}

@media all and (max-width: 768px) {
  .work {
    &__container {
      width: 100%;
    }
  }
  .company {
    &__descr {
      max-width: none;
    }
  }
}

@media all and (max-width: 480px) {
  .work {
    &__item {
      padding: 10px;
    }
  }
}
